@font-face {
  font-family: "SF Compact Display";
  src: url("fonts/SF-Compact-Display-Regular.eot");
  src: local("☺"), url("fonts/SF-Compact-Display-Regular.woff") format("woff"),
    url("fonts/SF-Compact-Display-Regular.ttf") format("truetype"),
    url("fonts/SF-Compact-Display-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  height: 100vh;
}
