.base-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  font-family: "SF Compact Display";

  .error {
    position: absolute;
    margin-top: 4px;
    font-size: 12px;
    color: #d00000;
  }

  .header {
    h1,
    h6 {
      font-family: "Roboto", sans-serif;
    }
    h1 {
      font-weight: 500;
      font-size: 47px;
      line-height: 55px;
      text-align: center;
      color: #1c1c1c;
    }
    h6 {
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      text-align: center;
      color: #1c1c1c;
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    margin-top: 50px;

    .form-group {
      margin: 35px 0;
      width: 374px;
      position: relative;

      .placeholder {
        position: absolute;
        top: -11px;
        color: #939292;
        font-size: 12px;
        &::after {
          content: attr(data-name);
        }
      }

      input {
        width: 100%;
        height: 27px;
        border: none;
        font-family: "SF Compact Display";
        border-bottom: 1px solid #dedede;
        &:focus {
          outline: none;
        }
        &::placeholder {
          color: #939292;
        }
        &.inputError {
          color: #d00000;
          border-bottom: 1px solid #d00000;
        }
        &.inputMaskError {
          border-bottom: 1px solid #d00000;
        }
      }

      img {
        position: absolute;
        right: 0;
        z-index: 100;
        cursor: pointer;
        transition: filter 250ms;
        &:hover {
          -webkit-filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.3));
          filter: drop-shadow(0 0 1px rgba(0, 0, 0, 0.3));
        }
      }
    }

    .checkbox {
      display: flex;
      align-items: center;
      justify-content: left;
      font-size: 13px;

      input {
        appearance: none;
        width: 17px;
        height: 17px;
        margin-right: 13px;
        border: 1px solid #dedede;
        border-radius: 0px;

        &:checked + span {
          content: "";
          position: absolute;
          top: 3px;
          left: 3px;
          width: 11px;
          height: 11px;
          background: #1c1c1c;
          z-index: -1;
        }
      }
    }

    .form {
      width: 100%;
    }

    .form button {
      position: relative;
      width: 100%;
      height: 44px;
      font-size: 17px;
      text-transform: uppercase;
      background-color: #dedede;
      color: #fff;
      border: none;
      outline: none;

      &.activeBtn {
        background-color: #1c1c1c;
      }
    }
  }

  .footer {
    margin: 30px;
    font-size: 17px;
    color: #939292;

    a {
      margin-left: 11px;
      color: #20201f;
      font-weight: 600;
      text-decoration: none;
    }
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none;
  -webkit-text-fill-color: #000;
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
  transition: background-color 5000s ease-in-out 0s;
}
